	module.exports = {"data":{"markdownRemark":{"html":"<blockquote class=\"large\"> moulin, the brainchild of Geekcorps volunteers Frederic Renet and Renaud Gaudin, started off as a side project of Geekcorps' Last Mile Initiative. Frederic and Renaud quickly developed an initial prototype of the system to run on a Nokia 770. Excited by the potential of making Wikipedia more widely accessible, Renaud volunteered for a second tour with Geekcorps and developed the current version of moulin which can be run off a CD.</blockquote>\n<p><img src=\"/static/images/moulin_612thumbnail.jpg\" alt=\"Moulin 612.Thumbnail\" height=\"76\" width=\"128\">Congrats to Geekcorps on developing a 400,000+ article no-images version of the wikipedia that fits snugly into a CD. The project is called <a href=\"http://www.moulinwiki.org/\">moulin</a>. I think the approach is audaciously simple, useful and humanitarian. And obviously lo-bandwidth friendly, once you've got a copy. Way to go!</p>","frontmatter":{"created_at":"January 31, 2007","path":"/2007/01/31/the-last-mile-wikipedia-launches","title":"The last mile wikipedia launches","tags":["moulin","geekcorps","humanitarian","wikipedia"]}}},"pathContext":{}}

